import React, { useState } from "react";
import InputNav from "./InputNav";
import MusicCard2 from "./MusicFilter/MusicCard2";
import MusicFilter from "./MusicFilter/MusicFilter";
import Modal from "./Profile/Modal";
import MusicForm from "./MusicFilter/MusicForm";
import ButtonMain from "../../components/ButtonMain";

import sim from "../../img/authors/hex.png";
import exd from "../../img/hex/hexvgon_babachaka.jpg";
import anm from "../../img/hex/2JoldXA2XH4_1.png";
import mir from "../../img/authors/mir.jpg";
import ppl from "../../img/authors/tea.jpg";
import mrn from "../../img/effunder/ааа.png";
import zvd from "../../img/effunder/ela.png";
import sng from "../../img/effunder/Без имени-1.png";
import lmg from "../../img/kors/Kors Naike Alo.png";
import mgk from "../../img/kors/Magicka.jpg";

function Music() {

    
     const [item, setItem] = useState([

        {
            id: 1,
            img: sim, 
            artist: 'hexvgon',
            musicants: 'hexvgon', 
            release: 'Simulacrium',
            apple: 'https://open.spotify.com/embed/album/2l0qVAjCUFwEVSl1b0ykY2?utm_source=generator',
        },
        {
            id: 2,
            img: exd,
            artist: 'hexvgon',
            musicants: 'hexvgon', 
            release:'Exodus', 
            apple: "https://embed.music.apple.com/us/album/exodus/1561078178?app=music&amp;itsct=music_box_player&amp;itscg=30200&amp;ls=1&amp;theme=dark" , 
        },
        {
            id: 3,
            img:anm,
            artist: 'hexvgon',
            musicants: 'hexvgon', 
            release:'Animus Erase', 
            apple: 'https://embed.music.apple.com/us/album/animus-erase/1525835371?theme=auto',   
    
        },
    
        {
            id: 4,
            img:mir,
            artist: 'Kors Naike',
            musicants: 'Kors Naike', 
            release:'Mir',  
            apple: "https://open.spotify.com/embed/album/2XjQBwHspuA58Wf1BzdonZ?utm_source=generator&theme=0",
        },
        {
            id:5,
            img: lmg,
            artist: 'Kors Naike', 
            musicants: 'Kors Naike', 
            release:'Let Me Go',  
            apple: 'https://embed.music.apple.com/us/album/let-me-go/1646428706?theme=auto',
        },
        {
            id: 6,
            img: mgk,
            artist: 'Kors Naike',
            musicants: 'Kors Naike', 
            release:'Magicka',
            apple: 'https://embed.music.apple.com/us/album/magicka-ep/1550034362?theme=auto',  
        },
    
        {
            id: 7,
            img: ppl,
            artist: 'пролитый чай',
            musicants: 'пролитый чай', 
            release:'Пепел',
            apple: 'https://embed.music.apple.com/us/album/%D0%BF%D0%B5%D0%BF%D0%B5%D0%BB/1614058247?theme=auto',  
        },
        {
            id: 8,
            img: mrn,
            artist: 'пролитый чай', 
            musicants: 'пролитый чай',
            release:'Марин Китагава',  
            apple: 'https://embed.music.apple.com/us/album/%D0%BC%D0%B0%D1%80%D0%B8%D0%BD-%D0%BA%D0%B8%D1%82%D0%B0%D0%B3%D0%B0%D0%B2%D0%B0-single/1620287022?theme=auto',
        },
        {
            id: 9,
            img: zvd,
            artist: 'пролитый чай',
            musicants: 'пролитый чай',
            release:'Над нами звёзды',  
            apple: 'https://embed.music.apple.com/us/album/%D0%BD%D0%B0%D0%B4-%D0%BD%D0%B0%D0%BC%D0%B8-%D0%B7%D0%B2%D1%91%D0%B7%D0%B4%D1%8B-single/1601606233?theme=auto',
        },
        {
            id: 10,
            img: sng,
            artist: 'пролитый чай', 
            musicants: 'пролитый чай',
            release:'Слёзы на глазах',  
            apple: 'https://embed.music.apple.com/us/album/%D1%81%D0%BB%D1%91%D0%B7%D1%8B-%D0%BD%D0%B0-%D0%B3%D0%BB%D0%B0%D0%B7%D0%B0%D1%85-single/1617978342?theme=auto',
        },
    
        
    
    ]);
  const menuItems = [...new Set(item.map((Val) => Val.artist))];
  const filterItem = (curcat) => {
    const newItem = item.filter((newVal) => {
      return newVal.artist === curcat;
    });
    setItem(newItem);
  };

  const createPost = (newPost) => {
    setItem([...item, newPost]);
    setModal(false)
  };

  const removePost = (post) => {
    setItem(item.filter((p) => p.id !== post.id));
  };

  const [modal, setModal] = useState(false);

  return (
    <InputNav
      content={
        <>
          <MusicFilter
            filterItem={filterItem}
            setItem={setItem}
            menuItems={menuItems}
          />

            
            <MusicCard2 remove={removePost} item={item} />

              <ButtonMain style={{margin:"4vh 0vw"}} title="Создать пост" onClick={()=>setModal(true)}/>
            <Modal visible={modal} setVisible={setModal}>
                 <MusicForm create={createPost}/>
            </Modal>
           
          
        </>
      }
    />
  );
}

export default Music;
